const mongoose = require('mongoose');
const {
    ObjectId
} = mongoose.Schema;

const {
    Schema
} = mongoose;
const TransUser = new Schema({
    user: {
        type: ObjectId,
        ref: 'User'
    },
    room: {
        type: ObjectId,
        ref: 'TransRoom'
    },
    language: {
        type: String,
        default: 'en'
    },
    trans: {
        type: String,
        default: 'en'
    },
    date: {
        type: Date,
        default: Date.now()
    }
});



const model = mongoose.model('TransUser', TransUser);

export default model;